import axios, { AxiosResponse } from 'axios'

import { type Semester, dateParams } from 'src/utils/date'

import type { ClubId } from 'src/models/Club'
import type { ClubMember, ClubMemberId, MemberLevel } from 'src/models/ClubMember'
import type { Form } from 'src/models/Form'
import type { Recruit } from 'src/models/Recruit'

interface GetFeeAmountParameters {
  clubId: ClubId
  year: number
  semester: Semester
  token: string
}

export interface GetFeeAmountResponseData {
  id: number
  level: MemberLevel
  amount: number
}

export const getFeeAmount = async ({ clubId, year, semester, token }: GetFeeAmountParameters) => {
  const response = await axios.get<GetFeeAmountResponseData[]>(
    `${process.env.REACT_APP_APIHOST}/club/${clubId}/fee/amount?year=${year}&semester=${semester}`,
    {
      headers: { Authorization: token }
    }
  )
  return response.data
}

interface PostFeeAmountParameters {
  clubId: ClubId
  year: number
  semester: Semester
  data: PostFeeAmountRequestData
  token: string
}

export interface PostFeeAmountRequestData {
  level: MemberLevel // TO DO: change into MemberLevel later on
  amount: number
}

export const postFeeAmount = async ({ clubId, year, semester, data, token }: PostFeeAmountParameters) => {
  await axios.post<null, AxiosResponse<null>, PostFeeAmountRequestData>(
    `${process.env.REACT_APP_APIHOST}/club/${clubId}/fee/amount?year=${year}&semester=${semester}`,
    data,
    {
      headers: { Authorization: token }
    }
  )
}

export interface PutFeeAmountRequestData {
  amount: number
}

interface PutFeeAmountParameters {
  clubId: ClubId
  year: number
  semester: Semester
  data: PutFeeAmountRequestData
  editFeeId: number
  token: string
}

export const putFeeAmount = async ({ clubId, year, semester, data, editFeeId, token }: PutFeeAmountParameters) => {
  await axios.put<null, AxiosResponse<null>, PutFeeAmountRequestData>(
    `${process.env.REACT_APP_APIHOST}/club/${clubId}/fee/amount/${editFeeId}?year=${year}&semester=${semester}`,
    data,
    {
      headers: { Authorization: token }
    }
  )
}

interface DeleteFeeAmountParameters {
  clubId: ClubId
  deleteId: number
  token: string
}

export const deleteFeeAmount = async ({ clubId, deleteId, token }: DeleteFeeAmountParameters) => {
  await axios.delete<null, AxiosResponse<null>, null>(
    `${process.env.REACT_APP_APIHOST}/club/${clubId}/fee/amount/${deleteId}`,
    {
      headers: { Authorization: token }
    }
  )
}

interface GetClubMemberParameters {
  clubId: ClubId
  token: string
  dateParams: typeof dateParams
}

type GetClubMemberResponseData = Array<ClubMember>

export const getClubMember = async ({ clubId, token, dateParams }: GetClubMemberParameters) => {
  const response = await axios.get<GetClubMemberResponseData>(
    `${process.env.REACT_APP_APIHOST}/club/${clubId}/member`,
    {
      headers: { Authorization: token },
      params: dateParams
    }
  )
  return response.data
}

export type PutClubMemberRequestBody = Array<{
  userId: ClubMemberId
  semesterInfo: { year: number; semester: Semester }
}>

interface PutClubMemberParameters {
  clubId: ClubId
  members: PutClubMemberRequestBody
  token: string
}

export const putClubMember = async ({ clubId, members, token }: PutClubMemberParameters) => {
  await axios.put<null, AxiosResponse<null>, PutClubMemberRequestBody>(
    `${process.env.REACT_APP_APIHOST}/club/${clubId}/member`,
    members,
    {
      headers: { Authorization: token }
    }
  )
}

interface GetApplyFormParameters {
  clubId: ClubId
  token: string // TODO: token 타입(Token) 분리하기
  dateParams: typeof dateParams
}

type GetApplyFormResponseData = Form

// TODO : url 뒤에 / 떼면 301 Moved Permanently 에러뜸
export const getApplyForm = async ({ clubId, token, dateParams }: GetApplyFormParameters) => {
  const response = await axios.get<GetApplyFormResponseData>(`${process.env.REACT_APP_APIHOST}/club/${clubId}/form/`, {
    headers: { Authorization: token },
    params: dateParams
  })

  return response.data
}

export type PostRequestBody = Form & Pick<Recruit, 'startAt' | 'endAt'>

export type PostParameters = {
  clubId: ClubId
  token: string
  dateParams: typeof dateParams
} & PostRequestBody

export const postApplyForm = async ({ clubId, token, dateParams, questions, startAt, endAt }: PostParameters) => {
  await axios.post<null, AxiosResponse<null>, PostRequestBody>(
    `${process.env.REACT_APP_APIHOST}/club/${clubId}/form`,
    { questions, startAt, endAt },
    {
      headers: { Authorization: token },
      params: dateParams
    }
  )
}

export type PutParameters = PostParameters

export type PutRequestBody = PostRequestBody

export const putApplyForm = async ({ clubId, token, dateParams, questions, startAt, endAt }: PutParameters) => {
  await axios.put<null, AxiosResponse<null>, PutRequestBody>(
    `${process.env.REACT_APP_APIHOST}/club/${clubId}/form`,
    { questions, startAt, endAt },
    {
      headers: { Authorization: token },
      params: dateParams
    }
  )
}
