import { useState } from 'react'
import { useAuthUser } from 'react-auth-kit'
import { Helmet } from 'react-helmet-async'
import { useParams } from 'react-router'

import { Container, type Theme, useMediaQuery } from '@mui/material'

import DateForm from 'src/components/DateForm'
import PageTitleWrapper from 'src/components/PageTitleWrapper'
import { ClubMemberForAuth } from 'src/models/ClubMember'
import { Semester, getCurrentSemester, getCurrentYear } from 'src/utils/date'

import PageHeader from '../PageHeader'
import MemberGrid from './MemberGrid'

function Member() {
  const { id } = useParams()
  const authUser = useAuthUser()
  const matches = useMediaQuery((theme: Theme) => theme.breakpoints.up('sm'))
  const [year, setYear] = useState(getCurrentYear())
  const [semester, setSemester] = useState<Semester>(getCurrentSemester())

  const clubId = Number(id)
  const club = authUser()?.clubs.filter((c: ClubMemberForAuth) => c.id === clubId)[0]

  const handleYearSet = (year: number) => {
    setYear(year)
  }

  const handleSemesterSet = (semester: Semester) => {
    setSemester(semester)
  }

  return (
    <>
      <Helmet>
        <title>{club.name} 회원 명단 - KLUB</title>
      </Helmet>
      <PageTitleWrapper>
        <PageHeader
          clubName={club.name}
          buttonText="회원 추가"
          secondButtonText="지원자 목록"
          link={`/manage/${clubId}/member/migration`}
          secondLink={`/manage/${clubId}/member/applicants`}
        />
      </PageTitleWrapper>
      <Container style={{ width: '100%' }} sx={{ px: matches ? 2 : 0 }}>
        <DateForm {...{ clubId, year, handleSemesterSet, semester, handleYearSet }} />
        <MemberGrid {...{ year, semester }} />
      </Container>
    </>
  )
}

export default Member
